import React, {Component} from 'react';
import '../style/About.css';
import Ranker from './Ranker';
import profile from "../assets/img/profile.jpg";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {Row, Col, Button} from 'react-bootstrap';
import {AboutEn, AboutIl, AboutIt} from './multilingualSource/About';


export default class About extends Component{

    constructor(props){
        super(props);
        this.toggleJava = this.toggleJava.bind(this);
        this.togglePhp = this.togglePhp.bind(this);
        this.toggleJavaScript = this.toggleJavaScript.bind(this);
        this.toggleCss = this.toggleCss.bind(this);
        this.toggleTools = this.toggleTools.bind(this);
        this.state = {
            javaOpen: false,
            phpOpen: false,
            javaScriptOpen: false,
            cssOpen: false,
            toolsOpen: false,
            about: {
                "en": AboutEn,
                "it": AboutIt,
                "il": AboutIl
            }
        }
    }

    toggleJava(){
        this.setState({
            javaOpen: !this.state.javaOpen
        });
    }

    togglePhp(){
        this.setState({
            phpOpen: !this.state.phpOpen
        });
    }

    toggleJavaScript(){
        this.setState({
            javaScriptOpen: !this.state.javaScriptOpen
        });
    }

    toggleCss(){
        this.setState({
            cssOpen: !this.state.cssOpen
        });
    }

    toggleTools(){
        this.setState({
            toolsOpen: !this.state.toolsOpen
        });
    }

    renderExpandedSectionDetails(section, alignment){
        let title = <h3 className="AboutTitle">{section.title}</h3>;
            let entries = section.entries.map((entry)=>{
                let icon = entry.iconSrc != null && entry.iconSrc !== undefined ? <img src={entry.iconSrc} className="svgLogo" alt={entry.name+" logo"}/> : entry.icon;
                return(
                    <>
                    <Row className="AboutDetailsRow">
                        <Col md="4" className="align-items-center justify-content-center d-inline-flex">
                            {icon}
                        </Col>
                        <Col md="8" className={"d-inline-flex align-items-center "+alignment}>
                            <div>
                            <h4>{entry.name}</h4>
                            <p className="AboutDetailsParagraph">{entry.description}</p>
                            </div>
                        </Col>
                    </Row>
                    
                    </>
                );
            });
            return(
                <>
                    {title}
                    {entries}
                </>
            );
    }

    render(){
        let languageSelected = (["en", "it", "il"].includes(this.props.language)) ? this.props.language : "en";
        let dir = this.state.about[languageSelected].direction;
        let alignment = (dir === "rtl") ? "text-right" : "text-left";
        let javaExpandedSection = this.state.about[languageSelected].javaExpanded.sections.map((section)=>{
            return this.renderExpandedSectionDetails(section, alignment);
        });
        let phpExpandedSection = this.state.about[languageSelected].phpExpanded.sections.map((section)=>{
            return this.renderExpandedSectionDetails(section, alignment);
        });
        //Get the languages array from the About file and map it as visual items
        const languages = this.state.about[languageSelected].languageArray.map(language =>{
            let cert = language.certification ? <div className="h2 text-center"><a href={language.certificationUrl} className="iconWrapper" target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon="award"/></a></div> : null;
            return(
                <Col xs={12} md={6} lg={4}>
                    <h2 className="text-center" dir={dir}>{language.languageName}</h2>
                    <Ranker score={language.rank} flipHalf={dir==="rtl"}/>
                    {cert}
                </Col>
            );
        });
        return(
            <div dir={dir} className="about">
                {/*description and profile picture*/}
                <Row className="mt-5 justify-content-center">
                    <Col xs={4} md={3} className="mb-5 mb-md-0 align-self-center">
                        <img id="profile-pic" src={profile} alt="Simone Bogni's face" className="img-fluid rounded-circle"/>
                    </Col>
                    <Col xs={12} md={8} className="offset-md-1 align-self-center">
                        <div className={"description "+alignment} dir={dir}>
                            {this.state.about[languageSelected].description}
                        </div>
                    </Col>
                </Row>

                {/*Language proficiency section*/}
                <Row className="mt-5">
                    <Col xs={12} className="text-center">
                        <h1 dir={dir}>{this.state.about[languageSelected].titleLanguage}</h1>
                    </Col>
                </Row>
                <Row className="mt-5 justify-content-center">
                    {languages}
                </Row>

                {/*Programming languages proficiency section*/}
                <Row className="mt-5">
                    <Col xs={12} className="text-center">
                        <h1 dir={dir}>{this.state.about[languageSelected].titleProgrammingLanguage}</h1>
                    </Col>
                </Row>
                <Row className="mt-5 justify-content-center">
                    {/* Java */}
                    <Col xs={12} md style={{minWidth: "33%"}}>
                        <Button block onClick={this.toggleJava} className="purpleButtonOutline">
                            <h2 className="text-center" dir={dir}><FontAwesomeIcon icon={["fab", "java"]} onClick={this.toggleJava}/> Java</h2>
                        </Button>
                    </Col>
                    <Col className="text-center" xs={12} style={{display: this.state.javaOpen ? "block" : "none",  marginBottom: "15px"}}>
                        {javaExpandedSection}
                    </Col>

                    {/* PHP */}
                    <Col xs={12} style={{display: this.state.phpOpen ? "block" : "none"}}>
                    </Col>
                    <Col xs={12}  md style={{minWidth: "33%"}}>
                        <Button block onClick={this.togglePhp} className="purpleButtonOutline">
                            <h2 className="text-center" dir={dir}><FontAwesomeIcon icon={["fab", "php"]}/> PHP</h2>
                        </Button>
                    </Col>
                    <Col className="text-center" xs={12} style={{display: this.state.phpOpen ? "block" : "none", marginBottom: "15px"}}>
                        {phpExpandedSection}
                    </Col>
                    {/* JavaScript*/}
                    <Col xs={12} style={{display: this.state.javaScriptOpen ? "block" : "none"}}>
                    </Col>
                    <Col xs={12} md style={{minWidth: "33%"}}>
                        <Button block onClick={this.toggleJavaScript} className="purpleButtonOutline">
                            <h2 className="text-center" dir={dir}><FontAwesomeIcon icon={["fab", "js"]}/> Javascript</h2>
                        </Button>
                    </Col>
                    <Col className="text-center" xs={12} style={{display: this.state.javaScriptOpen ? "block" : "none",  marginBottom: "15px"}}>
                        <h3>JavaScript test</h3>
                    </Col>

                    {/* CSS*/}
                    <Col xs={12} style={{display: this.state.cssOpen ? "block" : "none"}}>
                    </Col>
                    <Col xs={12} md style={{minWidth: "33%"}}>
                        <Button block onClick={this.toggleCss} className="purpleButtonOutline">
                            <h2 className="text-center" dir={dir}><FontAwesomeIcon icon={["fab", "js"]}/> CSS</h2>
                        </Button>
                    </Col>
                    <Col className="text-center" xs={12} style={{display: this.state.cssOpen ? "block" : "none",  marginBottom: "15px"}}>
                        <h3>CSS test</h3>
                    </Col>

                    {/* Tools*/}
                    <Col xs={12} style={{display: this.state.toolsOpen ? "block" : "none"}}>
                    </Col>
                    <Col xs={12} md style={{minWidth: "33%"}}>
                        <Button block onClick={this.toggleTools} className="purpleButtonOutline">
                            <h2 className="text-center" dir={dir}><FontAwesomeIcon icon="tools"/> Tools</h2>
                        </Button>
                    </Col>
                    <Col className="text-center" xs={12} style={{display: this.state.toolsOpen ? "block" : "none",  marginBottom: "15px"}}>
                        <h3>Tools test</h3>
                    </Col>
                </Row>
            </div>
        );
    }
}