import React, {Component} from 'react';
import PortfolioProject from './PortfolioProject';
import '../style/Portfolio.css';
import { Row, Col } from 'react-bootstrap';

export default class Portfolio extends Component{
    constructor(props){
        super(props);
        this.state = {
            categories: [
                {
                    name: "Projects in Java",
                    projects: [
                        {
                            title: "FaceDoor",
                            subtitle: null,
                            tags: ["Java", "UI", "Social Network", "Profile management"],
                            picture: "",
                            description: "Project at the first year of university.\nIt was requested to make the UI for a social network.\nIt doesn't provide a connection to the server but there are advanced functionality like: registration and login, profile search (with additional information provided to logged in users) and profile editing/deletion.\nThe project uses advanced data-structure (like the red and black tree) to make the profile lookup faster.",
                            buttonText: null,
                            buttonMode: null,
                            buttonLink: null,
                            id: "facedoor"
                        },
                        {
                            title: "WatchNeighbors",
                            subtitle: null,
                            tags: ["Java", "JavaFx", "Client-Server", "Geolocation", "Mail", "Profile management", "Database", "UML"],
                            picture: "",
                            description: "Project at the second year of university.\nIt is an application to help neighborhood watch groups.\nUser that are not logged in can only see reported activities on the map, while logged in users can see and insert an activity in their neighborhood on the map, as well as mark themselves as taking charge of it.\nWhen the user register to the platform, they receive an automated mail from the system. They have to click on a link in order to validate their profile.\nThe formal documentation was made in UML, the database used was MySql and OpenStreetMap was used for the map functionality. All the data is sanitized and preconfigured SQL statements were used to prevent SQL Injection attacks.\nThe Skeleton & Stub design pattern was used for the client-server interaction.",
                            buttonText: "Gallery",
                            buttonMode: "modal",
                            buttonLink: null,
                            id: "watchneighbors"
                        }
                    ]
                },
                {
                    name: "Data visualization with D3.js",
                    projects: [
                        {
                            title: "Treemap",
                            subtitle: "Kickstarter pladges",
                            tags: [],
                            picture: "https://codepen.io/simonebogni/pen/KKKdrmq/image/small.jpg",
                            description: "",
                            buttonText: "View it live!",
                            buttonMode: "link",
                            buttonLink: "https://codepen.io/simonebogni/full/KKKdrmq",
                            id: "d3-treemap"
                        },
                        {
                            title: "Choropleth Map",
                            subtitle: "US Education Attainment",
                            tags: [],
                            picture: "https://codepen.io/simonebogni/pen/yLLYQoP/image/small.jpg",
                            description: "",
                            buttonText: "View it live!",
                            buttonMode: "link",
                            buttonLink: "https://codepen.io/simonebogni/full/yLLYQoP",
                            id: "d3-choropleth-map"
                        },
                        {
                            title: "Heatmap",
                            subtitle: "Monthly global land-surface temperature",
                            tags: [],
                            picture: "https://codepen.io/simonebogni/pen/poojQWW/image/small.jpg",
                            description: "",
                            buttonText: "View it live!",
                            buttonMode: "link",
                            buttonLink: "https://codepen.io/simonebogni/full/poojQWW",
                            id: "d3-heatmap"
                        },
                        {
                            title: "Scatterplot chart",
                            subtitle: "Doping in professional bycicle racing",
                            tags: [],
                            picture: "https://codepen.io/simonebogni/pen/LJoadw/image/small.jpg",
                            description: "",
                            buttonText: "View it live!",
                            buttonMode: "link",
                            buttonLink: "https://codepen.io/simonebogni/full/LJoadw",
                            id: "d3-scatterplot-chart"
                        },
                        {
                            title: "Bar chart",
                            subtitle: "Gross Domestic Product",
                            tags: [],
                            picture: "https://codepen.io/simonebogni/pen/wEONVd/image/small.jpg",
                            description: "",
                            buttonText: "View it live!",
                            buttonMode: "link",
                            buttonLink: "https://codepen.io/simonebogni/full/wEONVd",
                            id: "d3-bar-chart"
                        }
                        
                    ]
                }
            ]
        }
    }

    render(){
        let languageSelected = (["en", "it", "il"].includes(this.props.language)) ? this.props.language : "en";
        let dir = (languageSelected === "il") ? "rtl" : "ltr";
        let alignment = (dir === "rtl") ? "text-right" : "text-left";
        let categories = this.state.categories.map(category=>{
            let projects = category.projects.map(project=>{
                return(
                    <Col xs={10} md={6} lg={4} key={project.id}>
                        <PortfolioProject 
                            direction={dir}
                            alignment={alignment}
                            title={project.title} 
                            subtitle={project.subtitle}
                            tags={project.tags} 
                            picture={project.picture} 
                            description={project.description}
                            buttonText={project.buttonText}
                            tagContainerClassName="ProjectTagContainer"
                            tagClassName="ProjectTag"
                            cardClassName="ProjectCard"
                            titleClassName="ProjectTitle"
                            descriptionClassName="ProjectDescription"
                            buttonContainerClassName="ProjectButtonContainer"
                            buttonMode={project.buttonMode}
                            buttonLink={project.buttonLink}
                        />
                    </Col>
                );
            });

            return(
                <>
                    <Row className="mt-5 justify-content-center">
                        <h2>{category.name}</h2>
                    </Row>
                    <Row className="justify-content-center">
                        {projects}
                    </Row>
                </>
            );
        });
        
        return(
            <div dir={dir} id="portfolio">
                <Row className="mt-5 justify-content-center">
                    <Col xs={10}>
                        <h1 className={"h1 text-center"} dir={dir}>This is some of my work</h1>
                    </Col>
                </Row>
                {categories}
            </div>
        );
    }
}