import React, {Component} from 'react';
import { Card, Button, Badge, Row, Col } from 'react-bootstrap';

export default class PortfolioProject extends Component{

    render(){
        let tags = this.props.tags.map(tag=>{
            return(
                <Badge pill className={this.props.tagClassName+" "+this.props.direction}>{tag}</Badge>
            );
        });
        let buttonPresent = this.props.buttonText != null;
        let descriptionClass = buttonPresent ? this.props.descriptionClassName : this.props.descriptionClassName + " extended";
        let button = this.props.buttonMode === "link" ? 
            <a className="btn btn-block" dir={this.props.direction} href={this.props.buttonLink} target="_blank" rel="noopener noreferrer">{this.props.buttonText}</a> : 
            this.props.buttonMode === "modal"? 
            <Button block dir={this.props.direction}>{this.props.buttonText}</Button> : 
            null;
        return(
            <Card className={this.props.cardClassName}>
                <Card.Img variant="top" src={this.props.picture}/>
                <Card.Body>
                    <Card.Title className={this.props.titleClassName}>{this.props.title}</Card.Title>
                    <h6 className={this.props.titleClassName}>{this.props.subtitle}</h6>
                    {
                        tags.length === 0 ? 
                        null :
                        <Row>
                            <Col xs={12} dir={this.props.direction} className={this.props.tagContainerClassName+" "+this.props.alignment}>
                                {tags}
                            </Col>
                        </Row>
                    }
                    <hr/>
                    <div className={descriptionClass}>
                        <Card.Text className={this.props.alignment} dir={this.props.direction}>
                            {this.props.description.split("\n").map(paragraph=>{
                                return(<p>{paragraph}</p>);
                            })}
                        </Card.Text>
                    </div>
                    {
                        this.props.buttonText != null ? 
                        <div className={this.props.buttonContainerClassName}>
                            {button}
                        </div> : 
                        null
                    }
                    
                </Card.Body>
            </Card>
        );
    }
}